.container {
    position: absolute;
    right: 20px;
    top: 22px;
}

.mainInfo {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.avatar img {
    height: 60px;
    width: 60px;
    border-radius: 100px;
}

.userInfo {
    margin-left: 10px;
    /* margin-top: 5px; */
}

@media only screen and (max-width: 900px) {
    .container {
        position: absolute;
        right: 30px;
        top: 28px;
        z-index: 1;
    }

    .avatar img {
        height: 70px;
        width: 70px;
        border-radius: 100px;
    }

}